/**
  * Modal component
  */
.c-modal {
  --modal-width: 90vh;
  --modal-max-width: 900px;
  --modal-opacity: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  padding: 20px;
  background-color: rgba(236, 236, 236, 0.8);
  opacity: var(--modal-opacity);
  z-index: 10;
  transition: 0.3s all ease;
  /**
  * Modal container
  */
  /**
  * Icon close button
  */
  /**
  * Icon close button
  */ }
  .c-modal__container {
    width: var(--modal-width);
    max-width: var(--modal-max-width);
    background-color: white;
    box-shadow: 0px 0px 2px rgba(133, 133, 133, 0.2);
    border: 1px solid #d7d6d6;
    border-radius: 3px;
    padding: 30px;
    position: relative;
    max-height: 95vh; }
    .c-modal__container--small {
      --modal-max-width: 400px; }
  .c-modal__title {
    padding-right: 40px;
    margin-bottom: 30px;
    font-size: 2rem;
    color: #9fa5ac;
    font-weight: 700; }
  .c-modal__button-close {
    position: absolute;
    top: 24px;
    right: 30px;
    color: #fb617f;
    transition: 0.3s all ease;
    font-size: 3rem;
    cursor: pointer; }
    .c-modal__button-close:hover {
      color: rgba(251, 97, 127, 0.8); }
  .c-modal__loading {
    width: 100%;
    height: 100%;
    background-color: rgba(255, 255, 255, 0.7);
    position: absolute;
    top: 0;
    left: 0; }
