/**
 * Button component
 */
.c-button {
  --btn-bg-color: #4680fe;
  --btn-bg-hover-color: rgba(70, 128, 254, 0.8);
  --btn-padding: 10px 20px;
  --btn-font-size: 1.6rem;
  padding: var(--btn-padding);
  color: white;
  font-weight: 700;
  font-size: var(--btn-font-size);
  cursor: pointer;
  border: none;
  border-radius: 3px;
  background-color: var(--btn-bg-color);
  transition: 0.2s all ease;
  /**
  * Modifiers
  */
  /**
  * Icons
  */ }
  .c-button:hover {
    background-color: var(--btn-bg-hover-color); }
  .c-button--small {
    --btn-padding: 7px 15px;
    --btn-font-size: 1.4rem; }
  .c-button--secondary {
    --btn-bg-color: #fb617f;
    --btn-bg-hover-color: rgba(251, 97, 127, 0.8); }
  .c-button--loading {
    pointer-events: none;
    cursor: default;
    outline: none; }
  .c-button__icon {
    margin-right: 10px; }
  .c-button__load-icon {
    margin-right: 10px;
    animation: spinner 1.5s linear infinite; }

@keyframes spinner {
  0% {
    transform: rotate(0deg); }
  100% {
    transform: rotate(360deg); } }
