/**
  * Dashboard Page
  */
.dashboard {
  width: 100%;
  max-width: 1480px;
  margin: 0 auto;
  /**
  * boards table
  */ }
  .dashboard__table {
    --dashboard-columns: repeat(2, 1fr);
    --dashboard-padding: 30px 15px;
    display: grid;
    grid-gap: 15px;
    grid-template-columns: var(--dashboard-columns);
    margin: 0 auto;
    padding: var(--dashboard-padding); }
    @media (min-width: 721px) {
      .dashboard__table {
        --dashboard-columns: repeat(3, 1fr);
        --dashboard-padding: 30px; } }
    @media (min-width: 1024px) {
      .dashboard__table {
        --dashboard-columns: repeat(4, 1fr); } }
    @media (min-width: 1280px) {
      .dashboard__table {
        --dashboard-columns: repeat(5, 1fr); } }
