/**
  * Tasklist component
  */
.c-tasklist {
  --taskList-width: 100%;
  --taskList-padding: 30px 15px 15px 15px;
  display: inline-flex;
  flex-direction: column;
  max-height: 100%;
  width: var(--taskList-width);
  position: relative;
  background-color: white;
  border: 1px solid #d7d6d6;
  border-radius: 2px;
  padding: var(--taskList-padding);
  margin-bottom: 15px;
  vertical-align: top;
  white-space: initial;
  /**
  * Header
  */
  /**
  * Footer
  */ }
  @media (min-width: 721px) {
    .c-tasklist {
      --taskList-width: 300px;
      --taskList-padding: 30px 10px 15px 15px;
      margin-bottom: 0; }
      .c-tasklist:not(:last-child) {
        margin-right: 20px; } }
  @media (min-width: 721px) {
    .c-tasklist__header {
      padding-right: 10px; } }
  .c-tasklist__header__title {
    font-size: 1.6rem;
    line-height: normal;
    font-weight: 700;
    padding-bottom: 20px;
    text-align: left; }
  .c-tasklist__header__remove {
    width: 25px;
    height: 25px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 1.5rem;
    position: absolute;
    top: -8px;
    right: -8px;
    background-color: #9fa5ac;
    color: white;
    border-radius: 50%;
    cursor: pointer;
    transition: 0.3s all ease; }
    .c-tasklist__header__remove:hover {
      background-color: #fb617f; }
  .c-tasklist__tasks {
    flex: 1;
    overflow-y: auto; }
    @media (min-width: 721px) {
      .c-tasklist__tasks {
        padding-right: 10px; } }
    .c-tasklist__tasks::-webkit-scrollbar-track {
      background-color: rgba(215, 214, 214, 0.3);
      border-radius: 10px; }
    .c-tasklist__tasks::-webkit-scrollbar {
      width: 10px;
      background-color: #fff; }
    .c-tasklist__tasks::-webkit-scrollbar-thumb {
      background-color: rgba(215, 214, 214, 0.9);
      border-radius: 10px; }
  .c-tasklist__footer {
    margin-top: 15px;
    padding-top: 15px;
    margin-right: 10px;
    border-top: 1px dashed #d7d6d6; }
    .c-tasklist__footer__add-task {
      font-weight: 700;
      font-size: 1.4rem;
      text-align: center;
      color: #9fa5ac;
      cursor: pointer;
      transition: 0.3s all ease; }
      .c-tasklist__footer__add-task:hover {
        color: #fb617f; }
