/**
 * Login Page
 */
/* Login wrap page */
.login-wrap {
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #5495de;
  flex-direction: column; }
  .login-wrap__header {
    margin-bottom: 20px; }
    .login-wrap__header__brand {
      color: white;
      font-size: 5.6rem;
      font-family: "Pacifico", cursive;
      text-shadow: 3px 3px #376fad; }
      .login-wrap__header__brand svg {
        filter: drop-shadow(1px 1px #376fad); }

/* Login box */
.login {
  width: calc(90vw - 7px);
  max-width: 600px;
  padding: 20px;
  background-color: white;
  box-shadow: #376fad 6px 6px 0px;
  border-radius: 4px;
  /**
  * Login error animation
  */
  /**
  * Form styles
  */ }
  @media (min-width: 721px) {
    .login {
      display: flex;
      padding: 30px; } }
  .login--shake {
    animation-name: shake-movement;
    animation-duration: 0.4s;
    transform-origin: 50% 50%;
    animation-timing-function: linear; }
  @media (min-width: 721px) {
    .login__form {
      flex: 1; } }
  .login__form:first-child {
    margin-bottom: 30px;
    padding-bottom: 30px;
    border-bottom: 1px dotted #9fa5ac; }
    @media (min-width: 721px) {
      .login__form:first-child {
        margin-bottom: 0;
        padding-bottom: 0;
        border-bottom: none;
        padding-right: 20px;
        margin-right: 20px;
        border-right: 1px dotted #9fa5ac; } }
  .login__title {
    font-size: 2.6rem;
    font-weight: 700;
    margin-bottom: 20px; }
  .login__field {
    margin-bottom: 10px; }

/**
  * Shake animation
  */
@keyframes shake-movement {
  0% {
    transform: translate(2px, 1px) rotate(0deg); }
  10% {
    transform: translate(-1px, -2px) rotate(-1deg); }
  20% {
    transform: translate(-3px, 0px) rotate(1deg); }
  30% {
    transform: translate(0px, 2px) rotate(0deg); }
  40% {
    transform: translate(1px, -1px) rotate(1deg); }
  50% {
    transform: translate(-1px, 2px) rotate(-1deg); }
  60% {
    transform: translate(-3px, 1px) rotate(0deg); }
  70% {
    transform: translate(2px, 1px) rotate(-1deg); }
  80% {
    transform: translate(-1px, -1px) rotate(1deg); }
  90% {
    transform: translate(2px, 2px) rotate(0deg); }
  100% {
    transform: translate(1px, -2px) rotate(-1deg); } }
