/**
  * Header component
  */
.c-header {
  --bg-header-color: white;
  --logo-size: 3.2rem;
  --header-padding: 1.5rem;
  --header-grid-template: 1fr 1fr 1fr;
  width: 100%;
  height: 60px;
  background-color: var(--bg-header-color);
  box-shadow: 10px -3px 10px rgba(133, 133, 133, 0.2);
  z-index: 1;
  /**
  * header center wrap
  */
  /**
  * Trollelo logo
  */
  /**
  * Header actions section
  */
  /**
  * Return button
  */ }
  @media (min-width: 721px) {
    .c-header {
      --header-padding: 1.5rem 3rem; } }
  .c-header__wrap {
    display: grid;
    grid-template-columns: var(--header-grid-template);
    max-width: 1480px;
    margin: 0 auto;
    padding: var(--header-padding); }
  .c-header__logo {
    font-size: var(--logo-size);
    color: #4680fe;
    font-family: "Pacifico", cursive;
    text-align: center;
    grid-column: 2 / span 1; }
  .c-header__actions {
    text-align: right;
    grid-column: 3 / span 1; }
  .c-header__return-btn {
    grid-column: 1 / span 1;
    font-size: 2.6rem; }
