/* Base */
/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline; }

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block; }

body {
  line-height: 1; }

ol, ul {
  list-style: none; }

blockquote, q {
  quotes: none; }

blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

/**
 * Base styles
 */
*,
*:before,
*:after {
  box-sizing: border-box;
  margin: 0;
  padding: 0; }

html {
  font-size: 62.5%;
  height: 100%; }

body {
  min-width: 360px;
  height: 100%;
  font-family: "Roboto", sans-serif;
  font-size: 1.4rem;
  color: #333; }

input,
button,
select,
textarea {
  font-family: "Roboto", sans-serif;
  font-size: 1.3rem;
  color: #333; }

p {
  line-height: normal; }

a,
.link {
  color: #4680fe;
  transition: 0.3s all ease;
  text-decoration: none; }
  a:hover,
  .link:hover {
    color: #fb617f; }

/* Modules */
/**
  * Subheader 
  */
.subheader {
  --subheader-columns: repeat(2, 1fr);
  --subheader-padding: 20px 15px;
  width: 100%;
  max-width: 1480px;
  margin: 0 auto;
  display: grid;
  grid-template-columns: var(--subheader-columns);
  border-bottom: 1px dashed rgba(215, 214, 214, 0.7);
  padding: var(--subheader-padding);
  /**
    * Subheader elements
    */ }
  @media (min-width: 721px) {
    .subheader {
      --subheader-padding: 20px 30px; } }
  @media (min-width: 1024px) {
    .subheader {
      --subheader-columns: repeat(3, 1fr); } }
  .subheader__content {
    display: flex;
    align-items: flex-start; }
  .subheader__actions {
    grid-column: 2 / span 1;
    text-align: right; }
    @media (min-width: 1024px) {
      .subheader__actions {
        grid-column: 3 / span 1; } }
  .subheader__icon {
    font-size: 2.4rem;
    background-color: #4680fe;
    color: white;
    padding: 8px 10px;
    border-radius: 3px;
    margin-right: 10px; }
  .subheader__title {
    font-size: 1.8rem;
    font-weight: 700;
    height: 100%;
    padding-top: 5px; }
    .subheader__title__user {
      display: block;
      font-size: 1.6rem;
      font-weight: 700;
      color: #9fa5ac; }

